li {
  list-style-type: none;
  text-align: center;
}

.app-wrapper {
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  width: 1200px;
  margin: 0 auto;
  grid-template-areas: 
  "h h"
  "h2 h2"
  "n c"
}


.app-wrapercontent {
  grid-area: c;
  background-color: rgb(72, 125, 175);
  
}



